/*
Header Navigation
*/
@import './mixin';
@import './variables';

.top-navigation {
    display: none;
    float: right;
    @include respond(small) {
        display: block;
        margin: 3vw 0;
    }
    @include respond(medium) {
        margin: 2rem 5rem;
    }
    a {
        color:$white;
        @include respond(small) {
            font-size: 3vw;
            font-weight: 400;
            margin: 2vw;
        }
        @include respond(large) {
            font-size: 1.5rem;
            margin: 1rem;
            text-decoration: none;
            display: inline-block;
            padding-bottom: 0.5rem;
            transition: .35s border-bottom ease;
        }
        &:hover {
            @include respond(large) {
                color: $red;
                border-bottom: 0.2rem $white solid;
            }
        }
    }
}  